.App {
  background: -webkit-gradient(linear, left bottom, left top, from(#111), to(#122));
  background: -webkit-linear-gradient(bottom, #111, #122);
  background: -o-linear-gradient(bottom, #111, #122);
  background: linear-gradient(to top, #111, #122);
  min-height: 100vh;
  overflow: hidden;
  text-align: center;
}

.banner {
  margin: auto;
  position: relative;
  max-width: 1020px;
  width: 92%;
}
.banner * {
  margin: auto;
}
.half-screen {
  display: -ms-flexbox;
  display: flex;
  width: 50%;
}

.section-bg {
  position: relative;
  z-index: 2;
}
.toAppear {
  overflow: hidden;
  opacity: 0;
  position: relative;
  -webkit-transform: scale(0.96);
      -ms-transform: scale(0.96);
          transform: scale(0.96);
  -webkit-transition: all 0.6s ease;
  -o-transition: all 0.6s ease;
  transition: all 0.6s ease;
}
.appear {   /*  animation  */
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}


.noscroll {
  overflow: hidden;
}

@media (max-width: 800px) {
  .section {
    padding: 0;
  }
}


